.form-panel{
    position: relative;  width: 100%;
    display: flex; flex-direction: column; align-content: flex-start; justify-content: flex-start;
    box-sizing: border-box;
    margin: 0 auto;
}

.form-panel.freeze::after{
    content: "";
    position: absolute; top: 0; left: 0;
    width: 100%; height: 100%;
    background-color: rgba(255, 255, 255, .4);
}

.form-panel > section {
    width: 100%;
    display: flex; flex-direction: column; align-content: flex-start; justify-content: flex-start;
}

.form-panel > section:nth-child(1) {
    display: flex;
    flex-direction: row;
    align-items: center;
    margin-bottom: 16px;
    column-gap: 6px;
    font-size: #555;
}

.form-panel > section:nth-child(1) > :nth-child(1){
    font-size: 14px;
}


.form-panel > section:nth-child(1) .cf-title{
    position: relative;
    font-size: 18px;
    text-align: left;
    font-weight: bold;
    width: fit-content;
}



/* grid  */
.form-panel .form-panel-grid{
    width: 100%; height: 100%;
    display: grid;
    column-gap: 8px;
    row-gap: 8px;
    grid-template-columns: repeat(12, 1fr);
    grid-row: auto;
}

.form-panel .form-panel-grid .grid-row { grid-column: 12 span; }
.form-panel .form-panel-grid .grid-row.r-lg1 { grid-column: 1 span; }
.form-panel .form-panel-grid .grid-row.r-lg2 { grid-column: 2 span; }
.form-panel .form-panel-grid .grid-row.r-lg3 { grid-column: 3 span; }
.form-panel .form-panel-grid .grid-row.r-lg4 { grid-column: 4 span; }
.form-panel .form-panel-grid .grid-row.r-lg5 { grid-column: 5 span; }
.form-panel .form-panel-grid .grid-row.r-lg6 { grid-column: 6 span; }
.form-panel .form-panel-grid .grid-row.r-lg7 { grid-column: 7 span; }
.form-panel .form-panel-grid .grid-row.r-lg8 { grid-column: 8 span; }
.form-panel .form-panel-grid .grid-row.r-lg9 { grid-column: 9 span; }
.form-panel .form-panel-grid .grid-row.r-lg10 { grid-column: 10 span; }
.form-panel .form-panel-grid .grid-row.r-lg11 { grid-column: 11 span; }
.form-panel .form-panel-grid .grid-row.r-lg12 { grid-column: 12 span; }


@media only screen and (max-width: 756px){
    .form-panel .form-panel-grid .grid-row{
        grid-column: 6 span!important;
    }
    .form-panel .form-panel-grid .grid-row { grid-column: 12 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm1 { grid-column: 1 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm2 { grid-column: 2 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm3 { grid-column: 3 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm4 { grid-column: 4 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm5 { grid-column: 5 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm6 { grid-column: 6 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm7 { grid-column: 7 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm8 { grid-column: 8 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm9 { grid-column: 9 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm10 { grid-column: 10 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm11 { grid-column: 11 span!important; }
    .form-panel .form-panel-grid .grid-row.r-sm12 { grid-column: 12 span!important; }

}